<template>
  <div class="tabs-wrapper">
    <keep-alive>
      <router-view class="tabs-wrapper-child"></router-view>
    </keep-alive>
    <footer>
      <router-link class="active-action" :to="item.path" v-for="(item, idx) in tabs" :key="idx">
        <i class="iconfont" :class="'icon'+item.name"></i>
        <p>{{item.name_cn}}</p>
      </router-link>
    </footer>
  </div>
</template>

<script>
  export default {
    name: "tabs",
    data() {
      return {
        tabs: []
      };
    },
    methods: {
      init() {
        this.getTabs();
      },
      getTabs() {
        const tabs = this.$router.options.routes.find(item => item.name === "tabs");
        if (!tabs) return;
        const opts = {
          assets: "资产",
          exchangeSLG: "SLG交易",
          market: "市场",
          find: "发现",
          setup: "设置"
        };
        this.tabs = tabs.children.sort((a, b) => {
          const order = Object.keys(opts);
          return order.indexOf(a.name) - order.indexOf(b.name);
        }).map(item => {
          return Object.assign(item, {
            name_cn: opts[item.name]
          });
        });
      }
    },
    mounted() {
      this.init();
    }
  };
</script>

<!--suppress CssInvalidPseudoSelector -->
<style scoped lang="scss">
  .tabs-wrapper {
    display: flex;
    flex-direction: column;

    &-child {
      width: 100%;
      height: 100%;
      overflow-x: hidden;
      display: flex;
      flex-direction: column;
      ::v-deep {
        .app-header {
          flex: 0 0 50px;
        }
        main {
          width: 100%;
          height: 100%;
          overflow-x: hidden;
        }
      }
    }

    footer {
      @include backgroundWhite;
      box-shadow: 0 2px 6px 4px rgba(0, 0, 0, 0.1);
      position: relative;
      z-index: 1;
      width: 100%;
      height: 60px;
      flex: 0 0 60px;
      display: flex;
      align-items: center;
      justify-content: space-around;
      > a {
        flex: 1;
        word-break: break-all;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        &.router-link-exact-active {
          color: $blue;
        }
        .iconfont {
          font-size: 20px;
        }
        p {
          font-size: 12px;
          margin-top: 5px;
        }
      }
    }
  }
</style>
